// Bootstrap
@import 'resources/sass/variables';
@import '~mdb-ui-kit/css/mdb.min.css';
@import '~bootstrap/dist/css/bootstrap-grid.min.css';
@import '../fontAwesomePro/fontawesome';


@import 'header';
@import 'home';
@import 'footer';
@import 'instagram';
@import 'item';
@import 'index';
@import 'login';
@import 'cart';
@import 'order';
@import 'profile';
@import "search";

@import 'product/catalog';
@import 'product/show';


*:active, *:hover {
  outline: none;
}

html, body, #app{
  height: 100%;
}

body {
  font-family: 'Tenor Sans', sans-serif;
  background: $body-bg;
  font-size:$font-size-base;
  line-height: $line-height-base;
  background: $white;
}

#app {
  display: flex;
  flex-direction: column;
}

.content {
  flex: 1 0 auto;
}

footer {
  flex-shrink: 0;
}

.scroll-blocked {
  overflow-y: hidden;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Tenor Sans', sans-serif;
}

.font-lato {
  font-family: 'Lato', sans-serif;
}

.font-tenor {
  font-family: 'Tenor Sans', sans-serif;
}

#index-page {
  .header {
    //position: absolute;
  }
}

#login-page, #register-page,
#password-request-page,
#password-reset-page {
  .header {
    display: none;
  }

  footer {
    display: none;
  }
}

.alert.info-alert {
  display: flex;
  display: none;
  align-items: center;
  position: fixed;
  right: 3em;
  top: 3em;
  z-index: 1000;

  button {
    margin-left: .5em;
  }

  @include media-breakpoint-down(md) {
    width: 90%;
    top: 1em;
    left: 5%;
  }
}

.form-outline-wrapper.invalid {
  .form-control {
    color: #2D3134;

    &:focus, &.active {
      &~ .form-notch {
        .form-notch-leading, .form-notch-middle, .form-notch-trailing {
          border-color: #f93154 !important;
        }
      }

      &~ .form-label {
        color: #f93154;
      }
    }

    &~ .form-notch {
      .form-notch-leading, .form-notch-middle, .form-notch-trailing {
        border-color: #f93154 !important;
      }
    }

    &~ .form-label {
      color: #f93154;
    }
  }

  .invalid-feedback {
    display: block;
  }
}

/* Анимации появления и исчезновения могут иметь */
/* различные продолжительности и динамику.       */
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active до версии 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

.font-lato {
  font-family: 'Lato', sans-serif !important;
}


.badge {
  line-height: unset;
  background: #E9EAEC;
  color: #2D3134;
  font-weight: 400;
  //margin-left: 1em;
}

.badge-notification {
  line-height: unset;
  font-weight: 500;
  position: absolute;
  right: -3px;
  top: 0;
  font-size: .6em;
  padding: 0 4px;
}
